.App {
  text-align: center;
}

.menu {
  padding: 25px 0;
  background-color: lightblue;

  & a {
    color: #333;
    margin: 0 5px;
    padding: 5px;
    font-size: 18px;
    font-weight: 500;
    text-decoration: none;
  }

  & a.active {
    color: crimson;
  }
}

.container {
  margin: 65px 0;
}

.title {
  margin: 50px 0;
}

.staff {
  margin: 50px 0;
  color: #555;
}

.staff>* {
  margin: 10px 0;
}

.staff a {
  display: block;
  margin-top: 20px;
  text-decoration: none;
  color: darkgrey;
  transition: 0.3s;
}

.staff a:hover {
  color: chocolate;
}