.weatherCard {
    margin: 50px auto;
    padding: 30px;
    border-radius: 7px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    max-width: 140px;
    text-align: center;
}

.weatherCard img {
    display: block;
    margin: 0 auto;
}

.weatherCard>* {
    margin: 10px 0;
}

.timezone {
    font-size: 13px;
    color: darkgray;
}